body {
    overflow: hidden;
}

  #preloader {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #fff;
    /* change if the mask should have another color then white */
    z-index: 99;
    /* makes sure it stays on top */
  }

  #status {
    width: 200px;
    height: 200px;
    position: absolute;
    left: 50%;
    /* centers the loading animation horizontally one the screen */
    top: 50%;
    /* centers the loading animation vertically one the screen */
    background-image: url(https://raw.githubusercontent.com/niklausgerber/PreLoadMe/master/img/status.gif);
    /* path to your loading animation */
    background-repeat: no-repeat;
    background-position: center;
    margin: -100px 0 0 -100px;
    /* is width and height divided by two */
  }

// 00. GOOGLE FONTS, IMPORTS, MIXINS, CUSTOM E COLORS +++++++++++

    // Google Fonts
    @import url('https://fonts.googleapis.com/css?family=Open+Sans+Condensed:700');

    // Mixins
    @import 'mixins';

    // Hover Animations
    @import url("hover.css");

    // Colors
    $branco: #fff;
    $preto: #000;
    $cinza: #b7b8b7;
    $cinza-escuro: #777777;
    $cinza-claro: #e2e3e4;
    $grafite: #4a4a4a;
    $azul: #2f3192;
    $azul-escuro: #000083;
    $azul-claro: #24a8d9;
    $azul-light: #00adec;
    $rosa: #d5258f;
    $rosa-light: #ec008c;

// FIM 00. IMPORTS, MIXINS, CUSTOM E COLORS +++++++++++


// 01. HEADER E FOOTER +++++++++++

    // header
    header {
        display: block;
        overflow: hidden;
        width: 100%;
        height: auto;
        background: $cinza-claro;
        padding: 50px 0;
        background: url(../img/bg-cinza.jpg) repeat;

        .cnt-mn {
            display: block;
            overflow: hidden;
            float: left;
            padding-top: 8px;

            a {
                display: block;
                float: left;
                margin-right: 60px;
                @include folio();
                font-size: 20px;
                color: $azul-escuro;
                text-transform: uppercase;
                font-weight: bold;
                text-decoration: none;
            }

            .hvr-underline-from-left:before {
                background: $azul-escuro;
            }

            .hvr-underline-from-left:hover:before, .hvr-underline-from-left:focus:before, .hvr-underline-from-left:active:before {
                right: 20px;
            }

            .lnk-mntop-ativo {
                background: $rosa-light;
                color: $branco;
                padding: 2px 5px;
            }

        }

        .cnt-sociais {
            display: block;
            overflow: hidden;
            float: right;

            a.lnk-social {
                display: block;
                float: left;
                width: 38px;
                height: 38px;
                background: $azul-escuro;
                font-size: 20px;
                color: $cinza-claro;
                text-decoration: none;
                text-align: center;
                padding-top: 5px;
                margin-left: 15px;
                @include border-radius(50%);
                @include transition(0.3s);

                &:hover {
                    background: $branco;
                    color: $azul-escuro;
                }

            }

        }
        .fixar {
            position:fixed;
            top:0;
            margin-top: 0px !important;
            padding:10px;
            z-index:200;
            background-color:$cinza-claro;
            width:100%;
            opacity:0.9;
            moz-opacity:0.9;
            filter: alpha(opacity=90);

            .cnt-mn{
                margin-left:-10px;
            }
        }

    }

    // footer
    footer {
        display: block;
        overflow: hidden;
        width: 100%;
        height: auto;
        text-align: center;
        background: #e1e1e1;
        padding: 40px 0;

        .img-responsive {
            margin-left: auto;
            margin-right: auto;
            height:91px;
        }

    }

// FIM 01. HEADER E FOOTER +++++++++++


// 02. WEBDOOR +++++++++++

