// 03. FORMULÁRIO +++++++++++

    .section-formulario {
        display: block;
        overflow: hidden;
        width: 100%;
        height: auto;
        background: $azul url(../img/seta-bg-form.jpg) center top no-repeat;
        padding: 50px 0 0 0;

        h2 {
            margin: 0;
            width: 100%;
            @include folio;
            font-size: 50px;
            color: $branco;
            text-transform: uppercase;
            text-align: center;
        }

        h3{
            margin: 0 0 54px 0;
            width: 100%;
            font-family: 'Folio', sans-serif;
            font-style: normal;
            text-rendering: optimizeLegibility;
            font-size: 32px;
            color: $branco;
            text-align: center;
            padding: 0 50px;

            span{
             font-size:28px;
            }
        }


        #frmResp {
            display: none;
            overflow: hidden;
            width: 100%;
            height: auto;
            text-align: center;

            .img-responsive {
                margin-left: auto;
                margin-right: auto;
            }

            .cnt-sociais {
                display: block;
                overflow: hidden;
                width: 100%;
                height: auto;
                text-align: center;
                margin: 30px 0;

                a.lnk-social-whatsapp {
                    display: inline-block;
                    overflow: hidden;
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    background: #0cc679;
                    color: $branco;
                    font-size: 30px;
                    padding: 4px 0 0 0;
                    margin: 0 30px 0 0;
                    @include transition(0.3s);

                    &:hover {
                        color: #0cc679;
                        background: $branco;
                    }

                }

                a.lnk-social-facebook {
                    display: inline-block;
                    overflow: hidden;
                    width: 50px;
                    height: 50px;
                    border-radius: 50%;
                    background: #4563b6;
                    color: $branco;
                    font-size: 30px;
                    padding: 4px 0 0 0;
                    @include transition(0.3s);

                    &:hover {
                        color: #4563b6;
                        background: $branco;
                    }

                }

            }

        }

        form {
            margin-top: 30px;
            width: 100%;
            text-align: left;

            .form-group {

                .form-control {
                    border: 1px solid $branco;
                    border-radius: 0;
                    background: none;
                    @include folio;
                    font-size: 18px;
                    color: $branco;
                    letter-spacing: 2px;
                }

                .form-control::-moz-placeholder {
                    color: $branco;
                    opacity: 1;
                }
                .form-control:-ms-input-placeholder {
                    color: $branco;
                }
                .form-control::-webkit-input-placeholder {
                    color: $branco;
                }

            }

            .checkbox {
                margin-bottom: 20px;
            }

            .checkbox label {
                //@include folio;
                @include opensans;
                font-size: 14px;
                color: $branco;
                text-align: left;
            }

            .checkbox label:after {
                content: '';
                display: table;
                clear: both;
            }

            .checkbox .cr {
                position: relative;
                display: inline-block;
                border: 2px solid $branco;
                border-radius: 0;
                width: 20px;
                height: 20px;
                float: left;
                margin-right: .5em;
                margin-left: -35px;
            }

            .checkbox .cr .cr-icon {
                position: absolute;
                font-size: .8em;
                line-height: 0;
                top: 50%;
                left: 15%;
                color: $branco;
            }

            .checkbox label input[type="checkbox"] {
                display: none;
            }

            .checkbox label input[type="checkbox"]+.cr>.cr-icon {
                opacity: 0;
            }

            .checkbox label input[type="checkbox"]:checked+.cr>.cr-icon {
                opacity: 1;
            }

            .checkbox label input[type="checkbox"]:disabled+.cr {
                opacity: .5;
            }

            .cnt-bt-enviar-form {
                display: block;
                overflow: hidden;
                width: 100%;
                height: auto;
                text-align: center;
                padding-top: 30px;
            }

            .btn-default {
                border: none;
                border-radius: 0;
                background: $rosa;
                @include folio;
                font-size: 24px;
                color: $branco;
                text-transform: uppercase;
                letter-spacing: 5px;
                padding: 15px 40px;
                margin: 30px auto 0 auto;
            }

            .hvr-sweep-to-bottom:before {
                background: $branco;
            }

            .hvr-sweep-to-bottom:hover, .hvr-sweep-to-bottom:focus, .hvr-sweep-to-bottom:active {
                color: $azul-escuro;
            }







            input[type="checkbox"] {
                display:block;
            }

            input[type="checkbox"] + label {
                position: absolute;
                color:#f2f2f2;
                top: -3px;
                left: -3px;
                @include opensans;
                font-size: 14px;
                color: $branco;
                text-align: left;
            }

            input[type="checkbox"] + label span {
                display:inline-block;
                width:24px;
                height:20px;
                margin:-2px 10px 0 0;
                vertical-align:middle;

                background:url(../img/bg-checkbox.png) left top no-repeat;
                //background:url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/210284/check_radio_sheet.png) left top no-repeat;
                cursor:pointer;
            }

            input[type="checkbox"]:checked + label span {
                background:url(../img/bg-checkbox.png) -25px top no-repeat;;
                //background:url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/210284/check_radio_sheet.png) -19px top no-repeat;
            }








        }

    }

// FIM 03. FORMULÁRIO +++++++++++



