    .section-video {
        display: block;
        overflow: hidden;
        width: 100%;
        height: auto;
        padding: 50px 0 0 0;

        h2 {
            width: 100%;
            @include folio;
            font-size: 37px;
            color: $azul-escuro;
            text-transform: uppercase;
            text-align: center;
            margin-bottom: 50px;
        }

        h3 {
            display: block;
            overflow: hidden;
            width: 34px;
            height: 34px;
            border: 1px solid $azul-escuro;
            @include border-radius(50%);
            padding: 5px 0 0 8px;
            margin: 0 auto 30px auto;

            .fa-angle-down {
                color: $azul-escuro;
            }

        }

        .cnt-video {
            display: block;
            overflow: hidden;
            position: relative;
            width: 100%;
            min-height: 741px;
            background: url(../img/bg-video.jpg) no-repeat center top;
            background-size: cover;
            text-align: center;

            .cnt-btn-play {
                display: block;
                overflow: hidden;
                width: 133px;
                height: 133px;
                border: 2px solid $branco;
                border-radius: 50%;
                padding: 18px;
                margin: 15% auto 0 auto;

                .lnk-play {
                    display: block;
                    overflow: hidden;
                    width: 90px;
                    height: 90px;
                    background: $branco;
                    color: $azul;
                    font-size: 40px;
                    border-radius: 50%;
                    padding-top: 18px;
                    padding-left: 8px;
                    @include transition(0.3s);
                    cursor: pointer;

                    &:hover {
                        color: $branco;
                        background: $azul;
                    }

                }

            }

            .cnt-social-share {
                display: block;
                overflow: hidden;
                position: absolute;
                top: 30px;
                right: 20%;
                width: auto;
                height: auto;

                a.lnk-share-whats {
                    display: inline-block;
                    overflow: hidden;
                    width: 60px;
                    height: 60px;
                    border-radius: 50%;
                    background: #0cc679;
                    color: $branco;
                    font-size: 34px;
                    padding: 7px 0 0 0;
                    margin: 0 10px 0 0;
                    @include transition(0.3s);

                    &:hover {
                        color: #0cc679;
                        background: $branco;
                    }
                }

                a.lnk-share-facebook {
                    display: inline-block;
                    overflow: hidden;
                    width: 60px;
                    height: 60px;
                    border-radius: 50%;
                    background: #4563b6;
                    color: $branco;
                    font-size: 30px;
                    padding: 10px 0 0 0;
                    margin: 0 10px 0 0;
                    @include transition(0.3s);

                    &:hover {
                        color: #4563b6;
                        background: $branco;
                    }

                }

                a.lnk-share-twitter {
                    display: inline-block;
                    overflow: hidden;
                    width: 60px;
                    height: 60px;
                    border-radius: 50%;
                    background: $azul-light;
                    color: $branco;
                    font-size: 30px;
                    padding: 10px 0 0 0;
