                    display:inline-block;
                    width:21px;
                    height:18px;
                    margin:-2px 10px 0 0;
                    vertical-align:middle;

                    background:url(../img/bg-checkbox2.png) left top no-repeat;
                    //background:url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/210284/check_radio_sheet.png) left top no-repeat;
                    cursor:pointer;
                }

                input[type="checkbox"]:checked + label span {
                    background:url(../img/bg-checkbox2.png) -23px top no-repeat;;
                    //background:url(https://s3-us-west-2.amazonaws.com/s.cdpn.io/210284/check_radio_sheet.png) -19px top no-repeat;
                }

            }

        }

        .acabamento-section {
            display: block;
            overflow: hidden;
            width: 100%;
            height: 15px;
            background: #eb008d;
            background: -moz-linear-gradient(left, #eb008d 50%, #00aceb 50%);
            background: -webkit-gradient(left top, right top, color-stop(50%, #eb008d), color-stop(50%, #00aceb));
            background: -webkit-linear-gradient(left, #eb008d 0%, #00aceb 50%);
            background: -o-linear-gradient(left, #eb008d 50%, #00aceb 50%);
            background: -ms-linear-gradient(left, #eb008d 50%, #00aceb 50%);
            background: linear-gradient(to right, #eb008d 50%, #00aceb 50%);
            filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#eb008d', endColorstr='#00aceb', GradientType=1 );
        }

    }

