    .section-mobilize-mobile {
        display: none;
    }

    .section-mobilize {
        display: block!important;
        overflow: hidden;
        width: 100%;
        height: auto;
        background: #292c97 url(../img/bg-mobilize.jpg) no-repeat center top;
        padding: 120px 0 0 0;

        h2 {
            display: block;
            overflow: hidden;
            width: 100%;
            @include folio;
            font-size: 80px;
            color: $branco;
            text-transform: uppercase;
            text-align: left;
            margin: 0 0 30px 0;
            padding: 0 0 0 0;
        }

        h3 {
            display: block;
            overflow: hidden;
            width: 100%;
            @include opensans;
            font-size: 30px;
            color: $branco;
            text-align: left;
            margin: 0 0 50px 0;
            padding: 0 0 25px 0;
            border-bottom: 5px solid $branco;
        }
        p a{
            color:$branco;
            text-decoration: underline;
        }

        .cnt-thumbs {
            display: block;
            overflow: hidden;
            width: 100%;
            height: auto;
            margin: 0 0 50px 0;

            .img-responsive {
                width: 100%;
            }

            .thumb-topo {
                display: block;
                overflow: hidden;
                position: relative;
                width: 100%;
                height: 38px;

                .topo-l {
                    display: block;
                    overflow: hidden;
                    position: absolute;
                    top: 0;
                    left: 0;
                    min-width: 50%;
                    height: auto;
                    @include opensans;
                    font-size: 20px;
                    color: $branco;
                    text-transform: uppercase;
                    padding: 5px 10px;
                    margin: 0;
                }

                .topo-r {
                    display: block;
                    overflow: hidden;
                    position: absolute;
                    top: 0;
                    right: 0;
                    width: 50%;
                    height: 38px;
                }

                .topo-full {
                    display: block;
                    overflow: hidden;
                    min-width: 100%;
                    height: auto;
                    @include opensans;
                    font-size: 20px;
                    color: $branco;
                    text-transform: uppercase;
                    padding: 5px 10px;
                    margin: 0;
                }

            }

            .thumb-rodape {
                display: block;
                overflow: hidden;
                width: 100%;
                height: auto;

                .topo-l {
                    display: block;
                    overflow: hidden;
                    float: left;
                    width: 50%;
                    height: 28px;
                }

                .topo-r {
                    display: block;
                    overflow: hidden;
                    float: left;
                    width: 50%;
                    height: auto;
                    text-transform: uppercase;

                    a.lnk-download {
                        display: block;
                        overflow: hidden;
                        width: 100%;
                        height: auto;
                        @include opensans;
                        font-size: 20px;
                        color: $branco;
                        text-transform: uppercase;
                        text-decoration: none;
                        padding: 0 0 0 10px;
                    }

                    .hvr-sweep-to-bottom:before {
                        background: $branco;
                    }

                    .hvr-sweep-to-bottom:hover, .hvr-sweep-to-bottom:focus, .hvr-sweep-to-bottom:active {
                        color: $azul-escuro;
                    }

                }

            }

            .thumb-sugestoes {
                display: block;
                overflow: hidden;
                width: 100%;
                height: 345px;
                background: $branco;
                padding: 45px 20px;
                @include opensans;
                font-size: 20px;
                color: $azul;
                line-height: 24px;

                a {
                    color: $azul;
                    text-decoration: underline;

                    &:hover {
                        text-decoration: none;
                    }

                }

            }

            .color-azul-light {
                background: $azul-light;
            }

            .color-rosa {
                background: $rosa;
            }

            .borda-top-azul-light {
                border-bottom: 5px solid $azul-light;
            }

            .borda-top-rosa {
                border-bottom: 5px solid $rosa;
            }

            .borda-bottom-azul-light {
                border-top: 5px solid $azul-light;
            }

            .borda-bottom-rosa {
                border-top: 5px solid $rosa;
            }

            .divisoria {
                clear: both;
                display: block;
                overflow: hidden;
                width: 100%;
                height: 80px;
            }

        }

        .cnt-cross {
            display: block;
            overflow: hidden;
            width: 100%;
            text-align: center;
            padding: 0 0 50px 0;
        }


    }

